
import React, { Component } from 'react';
import axios from 'axios';
import Navigator from '../companyNav.component';
import Content from './companyProfContent.component';


export default class CompanyProfile extends Component {
  constructor(props) {
    super(props);
    if(!localStorage.getItem('token') || !localStorage.getItem('company_id')){
      window.location.href = '/company-signin';
    }

    // Setting up functions
    this.onChangeName = this.onChangeName.bind(this);
    this.onChangeLoc = this.onChangeLoc.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.onChangeCompanyEmail = this.onChangeCompanyEmail.bind(this);
    this.onChangeCompanyPhone = this.onChangeCompanyPhone.bind(this);
    this.onSubmit = this.onSubmit.bind(this);

    // Setting up state
    this.state = {
      name: '',
      loc: '',
      description: '',
      email: '',
      phone: '',
      profPic: '',
    };
    // axios.get('http://localhost:4000/companies/user')
    //   .then((res) => {
    //     console.log(res.data);
    //     if (!res.data.isCompany) {
    //       window.location.href = '/company-signin';
    //     } else {
    //       this.setState({
    //         name: res.data.user.name,
    //         loc: res.data.user.loc,
    //         description: res.data.user.description,
    //         email: res.data.user.email,
    //         phone: res.data.user.phone,
    //         profPic: 'http://localhost:4000/companies/profPic/',
    //       });
    //     }
    //   });
    axios.defaults.headers.common['authorization'] = localStorage.getItem('token');
      const id = localStorage.getItem("company_id");
      axios.get('http://localhost:4000/companies/getCompany/' + id)
      .then((res) => {
        console.log(res.data[0].name == "MongoError");
        if (!(res.data[0].name == "MongoError")) {

          this.setState({
            name: res.data[0].name,
            loc: res.data[0].loc,
            description: res.data[0].description,
            email: res.data[0].email,
            phone: res.data[0].phone,
            profPic: 'http://localhost:4000/companies/profPic/' + id,
          });
        }
      });

    // axios.get('http://localhost:4000/companies/profPic')
    // .then(res => {
    //     console.log(res);
    //     this.setState({profPic: res});
    // })
  }


  onChangeName(e) {
    this.setState({ name: e.target.value });
  }


  onChangeLoc(e) {
    this.setState({ loc: e.target.value });
  }

  onChangeDescription(e) {
    this.setState({ description: e.target.value });
  }

  onChangeCompanyEmail(e) {
    this.setState({ email: e.target.value });
  }

  onChangeCompanyPhone(e) {
    this.setState({ phone: e.target.value });
  }


  onSubmit(e) {
    e.preventDefault();
    const {
      name, loc, description, email, phone,
    } = this.state;
    const companyObject = {
      id: localStorage.getItem("company_id"),
      name,
      loc,
      description,
      email,
      phone,

    };

    axios.put('http://localhost:4000/companies/update-company', companyObject)
      .then((res) => {
        console.log(res);
        if (res.data.name == "MongoError") {
          alert('Unsuccessful update');
        } else {
          alert('Successful update');
          window.location.href = '/company/profile';
        }
      });
  }

  onUpload(e) {
    console.log(this);
    e.preventDefault();
    const { files } = document.getElementById('INPUT_TAG');
    console.log(files);
    const formData = new FormData();
    formData.append('image', files[0]);
    // console.log(formData);
    axios.post('http://localhost:4000/companies/profPic/' + localStorage.getItem("company_id"), formData)
      .then((res) => {
        if (res.data.name == "MongoError") {
          alert('Unsuccessful update');
        } else {
          // alert(res.data);
          window.location.href = '/company/profile';
        }
      });
    console.log(files[0]);
  }

  render() {
    return (
      <div>
        <Navigator />
        <Content
          onSubmit={this.onSubmit}
          state={this.state}
          onChangeName={this.onChangeName}
          onChangeLoc={this.onChangeLoc}
          onChangeDescription={this.onChangeDescription}
          onChangeCompanyEmail={this.onChangeCompanyEmail}
          onChangeCompanyPhone={this.onChangeCompanyPhone}
          onUpload={this.onUpload}
        />

      </div>

    );
  }
}
