import {
    ADD_COMPANY,
    SIGNIN_COMPANY,
    LOGOUT_COMPANY,
  } from '../constants/company-action-types';
  
  
  export function addCompany(payload) {
    console.log('dispatching the action');
    return { type: ADD_COMPANY, payload };
  }
  export function signinCompany(payload) {
    console.log('dispatching the action');
    return { type: SIGNIN_COMPANY, payload };
  }
  
  export function logoutCompany(payload) {
    console.log('dispatching the action');
    return { type: LOGOUT_COMPANY, payload };
  }
  
