import axios from 'axios';
import {
  ADD_COMPANY,
  SIGNIN_COMPANY,
  LOGOUT_COMPANY,
} from '../constants/company-action-types';

export default function companyMiddleware() {
  return function (next) {
    return function (action) {
      if (action.type === ADD_COMPANY) {
        console.log(action.payload);
        axios.post('http://localhost:4000/companies/create-company', action.payload)
          .then((res) => {
            if (res.data.name == "MongoError") {
              alert('Unsuccessful signup; make sure email is unique');
            } else {
              alert('Successful signup');
              window.location.href = '/company-signin';
            }
          });
        }
    //   } else if (action.type === SIGNIN_STUDENT) {
    //     axios.post('http://localhost:4000/students/login', action.payload)
    //       .then((res) => {
    //         if (res.data.isStudent) {
    //           window.location.href = '/student/landing';
    //         } else {
    //           alert(res.data);
    //         }
    //       });
    //   } else if (action.type === LOGOUT_STUDENT) {
    //     axios.get('http://localhost:4000/students/logout')
    //       .then((res) => {
    //         console.log(res);
    //         window.location.href = '/';
    //       });
    //   } 

      return next(action);
    };
  };
}
