
import React, { Component } from 'react';
import axios from 'axios';
import Navigator from '../companyNav.component';
import Content from './studentSearchContent.component';


export default class CompanyStudentsTab extends Component {
  constructor(props) {
    super(props);


    // Setting up functions
    this.onChangeSearch = this.onChangeSearch.bind(this);

    // Setting up state
    this.state = {
      search: '',
      students: [],
    };
    if(!localStorage.getItem('token') || !localStorage.getItem('company_id')){
      window.location.href = '/company-signin';
    }
    axios.defaults.headers.common['authorization'] = localStorage.getItem('token');
  }
 
  onChangeSearch(e) {
    this.setState({ search: e.target.value }, () => {
      const { search } = this.state;
      if (search.length > 0) {
        const searchObject = {
          search,
        };
        axios.post('http://localhost:4000/students/search', searchObject)
          .then((response) => {
            // update the state with the response data
            console.log(response.data);

            this.setState({
              students: response.data,

            });
          });
      } else {
        this.setState({
          students: [],

        });
      }
    });
  }

  render() {
    return (
      <div>
        <Navigator />
        <Content
          onSubmit={this.onSubmit}
          state={this.state}
          onChangeSearch={this.onChangeSearch}
        />

      </div>

    );
  }
}
